* {
  max-width: 100vw;
  max-height: 100vh;
}

.root {
  height: 200vh;
}

.navigation_links {
  background-color: rgba(44, 44, 44);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.navigation_links {
  max-height: 100vh;
  min-width: 100vw;
}
div,
li,
a {
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 2vh;
  color: #edf0f1;
  text-decoration: none;
}

nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 2vw 1%;
  background-color: rgba(44, 44, 44);
}

.navigation_links {
  list-style: none;
  width: 100%;
  height: 100%;
}

.navigation_links li {
  display: inline-block;
  padding: 0px 1vw;
}

.navigation_links li:nth-child(1) {
  padding: 0 1vw 0 0;
}

.navigation_links li a {
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.navigation_links li a:hover {
  color: rgba(249, 212, 125, 1);
}

